<template>
	<div id="detail" ref="detail" >
		<detail-nav-bar @titleClick="titleClick" ref="nav"/>
		<!-- topImages  :top-images -->
		<scroll class="content"  @scroll="contentScroll" ref="scroll" :probe-type="3"  >
			<detail-swiper :top-images = "topImages" ></detail-swiper>
			<detail-base-info :goods="goods" @servicesShow="servicesShow"></detail-base-info>
			<detail-shop-info :shop="shop"></detail-shop-info>
			<detail-goods-info :detail-info="detailInfo" @detailImageLoad="detailImageLoad"></detail-goods-info>
			<detail-params-info ref="params" :params-info="paramsInfo"></detail-params-info>
			<detail-comment-info ref="comment" :comment-info="commentInfo"></detail-comment-info>
			<goods-list  ref="recommend"  :goods="recommends"></goods-list>
		</scroll>
		<back-top @click.native="backClick" class="back-top" v-show="isShowBackTop" ></back-top>
		<detail-bottom-bar class="detail-bottom-bar" @addCart = "addToCart"></detail-bottom-bar>
		<detail-services class="detail-services" v-if="servicesIsShow" ref="detailServices" :height="height"></detail-services>
		
		<toast :message="message" :show="show"></toast>
	</div>
</template>

<script>
	import DetailNavBar from './childComps/DetailNavBar.vue'
	import DetailSwiper from './childComps/DetailSwiper.vue'
	import DetailBaseInfo from './childComps/DetailBaseInfo.vue'
	import DetailShopInfo from './childComps/DetailShopInfo.vue'
	import DetailGoodsInfo from './childComps/DetailGoodsInfo.vue'
	import DetailServices from './childComps/DetailServices.vue'
	import DetailParamsInfo from './childComps/DetailParamsInfo.vue'
	import DetailCommentInfo from './childComps/DetailCommentInfo.vue'
	import DetailBottomBar from './childComps/DetailBottomBar.vue'
	
	import Scroll from 'components/common/scroll/Scroll.vue'
	import BackTop from 'components/content/backTop/BackTop.vue'
	import GoodsList from 'components/content/goods/GoodsList.vue'
	
	import {getDetail,getRecommend,Goods,Shop} from 'network/detail.js'
	import {debounce} from 'common/utils.js'
	
	import { mapActions } from 'vuex'
	
	
	import Toast from '../../components/common/toast/Toast.vue'
	
	export default {
		name:"Detail",
		components:{
			DetailNavBar,
			DetailSwiper,
			DetailBaseInfo,
			DetailShopInfo,
			DetailGoodsInfo,
			DetailServices,
			DetailParamsInfo,
			DetailCommentInfo,
			DetailBottomBar,
			
			Scroll,
			BackTop,
			GoodsList,
			Toast
		},
		data() {
			return{
				iid:null,
				topImages:[],
				// 
				goods:{},
				shop:{},
				detailInfo:{},
				paramsInfo:{},
				commentInfo:{},
				recommends:[],
				themeTopYs:[],
				getThemeTopY:null,
				currentIndex:0,
				height:10,
				servicesIsShow:false,
				isShowBackTop:false,
				
				message:'',
				show:false
			}
		},
		created() {
			//1. iid
			this.iid = this.$route.params.iid
			
			//2. iid 
			getDetail(this.iid).then(res => {
				// console.log(res)
				const data = res.result
				
				// 
				this.topImages = data.itemInfo.topImages
				
				// 
				this.goods = new Goods(data.itemInfo,data.columns,data.shopInfo.services)
				
				// 
				this.shop = new Shop(data.shopInfo)
				// console.log(this.shop)
				
				// 
				this.detailInfo = data.detailInfo
				
				// 
				this.paramsInfo = data.itemParams
				
				// 
				if(data.rate.cRate !== 0){
					this.commentInfo = data.rate.list[0]
				}
				// console.log(this.commentInfo)
				
				// offsetTop    this.$refs.params.$el  
				
				// created dom $nextTick
				//  $nextTick 
				// this.$nextTick(() =>{
					//    
					// DOM
					// offsetTop
					// offsetTop
				// 	this.themeTopYs.push(0);
				// 	this.themeTopYs.push(this.$refs.params.$el.offsetTop);
				// 	this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
				// 	this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
				// 	console.log(this.themeTopYs)
				// })
				
			})
			
			//3. 
			getRecommend().then(res => {
				this.recommends = res.data.list
				// console.log(this.recommends)
			})
			
			// 4.getThemeTopY 
			this.getThemeTopY = debounce(() =>{
				// debounce   
				this.themeTopYs = [];
				this.themeTopYs.push(0);
				this.themeTopYs.push(this.$refs.params.$el.offsetTop -44);
				this.themeTopYs.push(this.$refs.comment.$el.offsetTop - 44);
				this.themeTopYs.push(this.$refs.recommend.$el.offsetTop  -44);
				// push   
				this.themeTopYs.push(Number.MAX_VALUE);
				console.log(this.themeTopYs)
			},50)
		},
		mounted() {
			//   
			// this.height = this.$refs.bottom.offsetTop;
			let refresh = debounce(this.$refs.scroll.refresh,100)
			//    $bus main.js $bus  
			// 3.item
			 this.$bus.$on('detailItemImageLoad',()=>{
				refresh()
			 })
		},
		updated() {
			// updated    
			// this.themeTopYs = [];
			// this.themeTopYs.push(0);
			// this.themeTopYs.push(this.$refs.params.$el.offsetTop);
			// this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
			// this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
			// console.log(this.themeTopYs)
		},
		methods:{
			...mapActions(['addCart']),
			detailImageLoad(){
				// refresh();
				this.getThemeTopY()
			},
			titleClick(index){
				console.log(index)
				this.$refs.scroll.scrollTo(0,-this.themeTopYs[index],500)
			},
			
			servicesShow(){
				this.servicesIsShow = true
				setTimeout(()=>{
					this.$refs.detailServices.open()
				},10)
			},
			backClick(){
				this.$refs.scroll.scrollTo(0,0)
			},
			contentScroll(position){
				this.isShowBackTop = (-position.y) > 600
				
				// 1.y
				const positionY = - position.y
				
				// 2. positionY      themeTopYs
				
				// positionY   themeTopYs[0]  themeTopYs[1]  index = 0
				// positionY   themeTopYs[1]  themeTopYs[2]  index = 1
				// positionY   themeTopYs[2]  themeTopYs[3]  index = 2
				// positionY   themeTopYs[3]index = 3
				
				let length = this.themeTopYs.length
				// for in   for  i   for(let i in this.themeTopYs) 
				// hack   i < length ;   i < length - 1 ;
				  for(let i = 0; i < length - 1;i++){
					// 
					// if(this.currentIndex !== i &&
					// 	(i < length - 1 && positionY >= this.themeTopYs[i] && positionY < this.themeTopYs[i+1]) |
					// 	//      i+1 
					// 	(i === length - 1 && positionY >= this.themeTopYs[i])){
					// 		// console.log(i)
					// 		this.currentIndex = i
					// 		this.$refs.nav.currentIndex = this.currentIndex
					// }
					
					// hack  
					//   Number.MAX_VALUE   
					// js  Number.MAX_VALUE 
					if(this.currentIndex !== i && (i < length - 1 && positionY >= this.themeTopYs[i] && positionY < this.themeTopYs[i+1])){
						this.currentIndex = i
						this.$refs.nav.currentIndex = this.currentIndex
					}
				}
			},
			addToCart(){
				// 
				const product = {}
				product.image = this.topImages[0];
				product.title = this.goods.title;
				product.desc = this.goods.desc;
				product.price = this.goods.realPrice;
				product.iid = this.iid;
				
				// 2.
				this.addCart(product).then(res =>{
					// this.show = true;
					// this.message = res;
					
					// setTimeout(()=>{
					// 	this.show = false;
					// 	this.message =''
					// },1500)
					this.$toast.show(res,1000)
				})
				
				// this.$store.dispatch('addCart',product).then(res =>{
				// 	console.log(res)
				// })
			}
		}
	}
</script>

<style scoped>
	#detail {
		position: relative;
		z-index:9;
		background-color: #FFFFFF;
		height: 100vh;
	}
	
	.content {
		/*  */
		height: calc(100% - 44px - 52px );
		overflow: hidden;
	}
</style>
